export default {
	components: {},
	data() {
		return {
			devid:'477077ac-04d3-4574-aea5-b7ca98b1415e',
			devinfo:null,
			userinfo:null,
			illumination:666,//光照
			sound:233,//噪音
			LED:true,
			signal:4,//信号强度
			termperature:25,
			humidity:30,
		}
	},
	onLoad:function(e){
		
	},
	methods: {
		getInfo(){
			uni.showLoading({
				title: '数据加载中...',
				mask: false
			});
			uni.request({
				url: this.globalVal.default_url.devInfo,
				method: 'POST',
				data: {
					deviceId:this.devid
				},
				success: res => {
					console.log(res);
					if(200 == res.statusCode){
						this.devinfo = JSON.stringify(res.data);
						if(undefined == res.data.error_code){
							this.userinfo = res.data.services[0].data.infostring;
							let val = JSON.parse(this.userinfo);
							this.termperature = val.T;
							this.humidity = val.H;
							
							console.log("温度: "+val.T+"C"+" 湿度: "+val.H+"%");
						}
						
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
				}
			});
		}
	}
}
